

 
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import React, { useEffect, useRef, useState } from "react";

import ChatBot from './chatbot/ChatBot';
import MainPage from './mainPage/MainPage';
import Summary from './summary/Summary';
import FeedHome from './community/feed/route/FeedHome'
import SearchHome from './community/feed/route/SearchHome'

function App() {

  //    SampleData
  const [columns, setColumns] = useState(["", "", "", "", " ", " "]);
  const [tPlanData, setTplanData] = useState([
      {planName:"A", st_dt:"2022-03-01", end_dt:"2022-03-25", root:", ", shareStatus:"N"},
      {planName:"B", st_dt:"2022-03-04", end_dt:"2022-03-21", root:", , ", shareStatus:"N"},
      {planName:"C", st_dt:"2022-05-01", end_dt:"2022-06-02", root:", ", shareStatus:"Y"}
  ]);

  //    SampleData
  const [columnsHit, setColumnsHit] = useState(["", " "]);
  const [hitPlaceData, setHitPlaceData] = useState([
      {name:"", hitCnt:"5000"},
      {name:"", hitCnt:"652"},
      {name:"", hitCnt:"200"}
  ]);
  
  return (
    <div className="App">
      <BrowserRouter>
        <MainPage/>
        <ChatBot/>
        <Routes>
          <Route path="/" element={<Summary columns={columns}
                                            tPlanData={tPlanData}
                                            columnsHit={columnsHit}
                                            hitPlaceData={hitPlaceData}
                                            />}></Route>
           <Route path={`/feed`} element={<FeedHome />}></Route>
            <Route path={`/search/:searchValue`} element={<SearchHome />}></Route>                                 
          {/* <Route path="/plan" element={<Plan/>}></Route>
          <Route path="/community" element={<Community/>}></Route>
          <Route path="/mypage" element={<Mypage/>}></Route>
          <Route path="/logout" element={<Logout/>}></Route> */}
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
